import { createStore } from 'vuex'

export type Attribute = 'authority' | 'trade' | 'combat';

export interface Action {
  attribute: Attribute;
  actionType: 'add' | 'subtract' | 'set';
  amount: number;
  previousValue: number;
}

export interface State {
  attribute: Attribute
  authority: number;
  trade: number;
  combat: number;
  actions: Action[];
}

export default createStore<State>({
  state: {
    attribute: 'authority',
    authority: 0,
    trade: 0,
    combat: 0,
    actions: []
  },
  mutations: {
    select(state, value: Attribute) {
      state.attribute = value;
      localStorage.setItem('state', JSON.stringify(state));
    },
    add(state, value: number) {
      if (state.attribute && !isNaN(value)) {
        const action: Action = {
          attribute: state.attribute,
          actionType: 'add',
          amount: value,
          previousValue: state[state.attribute]
        };
        state[state.attribute] += value;
        state.actions.push(action);
        localStorage.setItem('state', JSON.stringify(state));
      }
    },
    subtract(state, value: number) {
      if (state.attribute && !isNaN(value)) {
        const action: Action = {
          attribute: state.attribute,
          actionType: 'subtract',
          amount: value,
          previousValue: state[state.attribute]
        };
        state[state.attribute] -= value;
        state.actions.push(action);
        localStorage.setItem('state', JSON.stringify(state));
      }
    },
    set(state, value: number) {
      if (state.attribute && !isNaN(value)) {
        const action: Action = {
          attribute: state.attribute,
          actionType: 'set',
          amount: value,
          previousValue: state[state.attribute]
        };
        state[state.attribute] = value;
        state.actions.push(action);
        localStorage.setItem('state', JSON.stringify(state));
      }
    },
    undo(state) {
      const previous = state.actions.pop();
      state[state.attribute] = previous?.previousValue ?? 0;
      localStorage.setItem('state', JSON.stringify(state));
    },
    load(state) {
      const storage = localStorage.getItem('state');
      if (storage) {
        try {
          const storageState = JSON.parse(storage);
          Object.assign(state, storageState);
        } catch (error) {
          console.error(error);
        }
      }
    }
  },
  actions: {
  },
  modules: {
  }
})
