
import { defineComponent } from 'vue';
import { mapMutations, mapState } from 'vuex';
import { tsParticles } from "tsparticles";
import { Attribute } from './store';
import particlesConfig from './particles.config';

export default defineComponent({
  name: 'App',
  data() {
    return {
      value: ''
    }
  },
  computed: {
    ...mapState(['attribute', 'authority', 'trade', 'combat']),
    parsedValue(): number {
      return parseInt(this.value, 10);
    }
  },
  mounted() {
    tsParticles.load("tsparticles", particlesConfig);
    this.load();
  },
  methods: {
    ...mapMutations(['select', 'add', 'subtract', 'set', 'undo', 'load']),
    onInput(value: number) {
      this.value += value.toString();
    },
    onDelete() {
      this.value = this.value.substring(0, this.value.length - 1);
    },
    onClear() {
      this.value = '';
    },
    onSelect(value: Attribute) {
      this.select(value);
    },
    onAdd() {
      this.add(this.parsedValue);
      this.onClear();
    },
    onSubtract() {
      this.subtract(this.parsedValue);
      this.onClear();
    },
    onSet() {
      this.set(this.parsedValue);
      this.onClear();
    },
    onUndo() {
      this.undo();
    }
  }
});
